import Keycloak from "keycloak-js";
import AuthProvider from './auth';

const keycloak = new Keycloak({
  url: process.env.REACT_APP_AUTH_SERVER_URL,
  realm: process.env.REACT_APP_REALM || "cns",
  "ssl-required": "external",
  "public-client": true,
  clientId: "sxp",
});

const initKeycloak = (onAuthenticatedCallback) => {
  keycloak
    .init({
      onLoad: "check-sso",
      silentCheckSsoRedirectUri:
        window.location.origin + "/silent-check-sso.html",
      pkceMethod: "S256",
    })
    .then((authenticated) => {
      if (authenticated) {
        AuthProvider.postLoginSteps(getUsername(), getUserEmail());
        onAuthenticatedCallback();
      } else {
        doLogin();
      }
    });
};

const doLogin = keycloak.login;

const clearAllLocalstorage = () => {} 
const doLogout = () => {
  clearAllLocalstorage();
  keycloak.logout();
};

const getToken = () => keycloak.token;

const isLoggedIn = () => !!keycloak.token;

const updateToken = (successCallback) =>
  keycloak.updateToken(5).then(successCallback).catch(doLogin);

const getUsername = () => keycloak.tokenParsed?.preferred_username;

const getUserEmail = () => keycloak.tokenParsed?.email;

const hasRole = (roles) => roles.some((role) => keycloak.hasRealmRole(role));

const UserService = {
  initKeycloak,
  doLogin,
  doLogout,
  isLoggedIn,
  getToken,
  updateToken,
  getUsername,
  getUserEmail,
  hasRole,
};

export default UserService;
