import HttpService from "../auth/HttpService";
import UserService from "../auth/KeycloakLogin";

const CHANNEL_ADAPTER_URL = process.env.REACT_APP_SOCKET_URL;
const PROJECT_ID = "62257b97d3f3cc2e4e185114";

const axios = HttpService.getAxiosClient();
let headers = {
  "X-Parse-Application-Id": "615e9d31c2e7a08128bea4e2",
  "X-Parse-Master-Key": "cns@1234",
};

export const getData = async (url) => {
  let resp = await axios.get(url, {
    headers: headers,
  });
  return resp;
};

export const saveData = async (obj) => {
  let res = await axios.post(obj.url, obj.body, {
    headers: headers,
  });
  return res;
};

export const putData = async (data) => {
  const url = `${CHANNEL_ADAPTER_URL}state/projectId/${PROJECT_ID}`;
  const config = {
    method: "PUT",
    url,
    headers: {
      Authorization: `Bearer ${UserService.getToken()}`,
    },
    data,
  };
  return axios(config);
};

export const postData = async (data) => {
  const url = `${CHANNEL_ADAPTER_URL}message/process/projectId/${PROJECT_ID}`;
  const config = {
    method: "POST",
    url,
    headers: {
      Authorization: `Bearer ${UserService.getToken()}`,
    },
    data,
  };
  return axios(config);
};
