import React, {Component} from "react";
import { getData, putData, postData } from "./apihandler";
import './form.css'

class FormFields extends Component{
    state={
        nurseData:[],
        doctorData:[]
    }
    handleChange = (e) => {
        this.setState({[e.target.name]: e.target.value});
    }

    componentDidMount = () => {
        this.getNurseData();
        this.getDoctorData();
    }

    getNurseData = async() => {
        const url = 'https://sxp.v1.techsophy.com/backend-services/app/615e9d31c2e7a08128bea4e2/classes/Nurse';
        const initialData = await getData(url);
        this.setState({nurseData: initialData.data.results})
    }

    getDoctorData = async() => {
        const url = 'https://sxp.v1.techsophy.com/backend-services/app/615e9d31c2e7a08128bea4e2/classes/Doctor';
        const initialData = await getData(url);
        this.setState({doctorData: initialData.data.results})
    }


    handleSubmit = async() => {
        const stateObj = {
            name: this.state.name,
            age: this.state.age,
            gender: this.state.gender,
            address: this.state.address,
            contactNumber: this.state.contactNumber,
            nurse: this.state.nurse,
            doctor: this.state.doctor,
        };
        console.log(stateObj);
        const data = { state: stateObj };
        try {
            await putData(data);
            const journey = await postData({message: "hi"});
            alert('Patient Registered');
        } catch (err) {
            console.log(err);
        }
    }

    render(){
        return(
            <div className="formFields">
                <div>
                    <label>Name</label>
                    <input type="text" placeholder="Name" name="name" onChange={this.handleChange}/>
                </div>
                <div>
                    <label>Age</label>
                    <input type="text" placeholder="age" name="age"  onChange={this.handleChange}/>
                </div>
                <div>
                    <label>Gender</label>
                    <select name="gender" onChange={this.handleChange}>
                        <option value="">Select Gender</option>
                        <option value="male">Male</option>
                        <option value="female">Female</option>
                        <option value="other">other</option>
                    </select>
                </div>
                <div>
                    <label>Address</label>
                    <textarea placeholder="Address" name="address" onChange={this.handleChange}></textarea>
                </div>
                <div>
                    <label>Contact Number</label>
                    <input type="text" placeholder="Phone Number" name="contactNumber" onChange={this.handleChange}/>
                </div>
                <div>
                    <label>Nurse</label>
                    <select name="nurse" onChange={this.handleChange}>
                        <option key="val0" value="0">Select Nurse</option>
                        {this.state.nurseData.map((d,i) => (
                            <option key={d.objectId+i} value={d.objectId}>{d.name}</option>
                        ))}
                    </select>
                </div>
                <div>
                    <label>Doctor</label>
                    <select name="doctor" onChange={this.handleChange}>
                        <option key="val0" value="0">Select Doctor</option>
                        {this.state.doctorData.map((d,i) => (
                            <option key={d.objectId+i} value={d.objectId}>{d.name}</option>
                        ))}
                    </select>
                </div>
                <div className="text-center" onClick={this.handleSubmit}><button>Submit</button></div>
            </div>
        );
    }
}

export default FormFields;